import Vue from 'vue';
import App from './App.vue';
import animated from 'animate.css'; // npm install animate.css --save
import axios from 'axios';
import qs from 'qs'
import mavonEditor from 'mavon-editor' //
import 'mavon-editor/dist/css/index.css'
import router from './router/router' //
import './plugins/element.js'


//mavon markdown
Vue.use(mavonEditor)

//axios
Vue.prototype.axios = axios;
Vue.prototype.HOST = '/api'
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'; // 
axios.defaults.baseURL = process.env.VUE_APP_URL || '/api'
//axios
axios.interceptors.request.use(function (config) {
  config.headers['Content-Type'] = 'application/x-www-form-urlencoded'
  if (config.method === 'post') { // post
    config.data = qs.stringify({
      ...config.data //axiospostjson,qs.
    })
  }
  return config;
}, function (error) {
  loadinginstace.close()
  return Promise.reject(error);
})

axios.interceptors.response.use(res => {
  return res //
}, err => {
  if (err.response.data.message) {
    Vue.prototype.$message({ //messageerrmessage
      type: 'error', //err
      message: err.response.data.message //
    })
  }
  return Promise.reject(err) //promise
})


//axios
axios.interceptors.response.use(function (response) {
  return response;
}, function (error) {
  return Promise.reject(error);
});



Vue.use(animated)
Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App),

}).$mount('#app')